.login-container{
    display: flex;
    justify-content: center;
    align-items: center;
}
.form-box{
    width: 300px;
    box-shadow: 0px 2px 10px rgba(39, 39, 39, 0.2);
    padding: 40px 30px 20px;
    border-radius: 10px;
    border-top: 5px solid var(--primary-color);
    background-color: #fff
}
.form-box .input{
    outline: 0;
    border: 1px solid var(--primary-color);
    border-radius: 5px;
    padding: 10px 5px;
    width: 95%;
    font-size: 15px;
    
}
.form-box input{
    margin-bottom: 5px;
}
.form-box input[type=checkbox]{
    margin: 20px 0px 10px;
}
.form-box .btn{
    display: block;
    padding: 10px;
    text-transform: capitalize;
    margin: 25px 0 15px;
    width: 100%;
}
.form-box label{
    display: block;
    margin: 5px 0;
}
.form-box .label{
    width: 100%;
    text-align: center;
    font-size: 20px;
    padding: 10px 0;
    margin-bottom: 15px;
    background-color: #98969684;
}
.form-box .icon-img{
    margin-inline: 8vw;
    width: 40px;
    box-shadow: 0 1px 5px var(--shadow-color);
    padding: 3px 8px;
    border-radius: 50%;
}
.form-box img{
    cursor: pointer;
    margin-top: 6px;
    width: 100%;
}
@media(max-width:550px){
    .form-box .icon-img{
        margin-inline: 25vw;
    }
}
