.history{
    display: block;
}
.history h2{
    color: var(--text-light);
    margin-bottom: .5em;
}
.history-item{
    background-color: var(--primary-color);
    display: inline-block;
    padding: 0.5em 1em;
    margin: 0 1em 1em 0;
    color: #fff;
    border-radius: 30px;
    box-shadow: 0 3px 6px var(--shadow-color);
    animation: 0.5s slideRight forwards;
}
.search{
    display: flex;
    align-items: center;
    justify-content: flex-end;
    height: 40px;
}
.search input{
    height: 35px;
    border-radius: 5px 0 0 5px;
    min-width: 250px;
    padding: 0 .5em;
    outline: 0;
    border: 1px solid #4d4d4d;
}
.search .btn{
    border-radius: 0 5px 5px 0;
    padding: 5px 8px;
    height: 37px;
}